.App {
  text-align: center;
  align-items: center;
  width: 80vh;
}

.App-logo {
  height: 40vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-answer {
  background-color: blue;
  font-size: calc(10px + 2vmin);
  color: black;
  align-items: center
}
.App-header {
  background-color: #282c34;
  height: 50vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: center
}

.App-link {
  color: #61dafb;
}

.App-refresh {
  background-color: white;
  font-size: calc(10px + 2vmin);
    align-items: center

}
.App-refresh-button {
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: grey;
  font-size: calc(10px + 2vmin);
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
